import React, { Component } from 'react';
import './index.css';
import Panel from './painel';

const Footer = () => {
  return (<div className="flex flex-center">
            <small>
              &copy;{new Date().getFullYear()}&nbsp;-&nbsp;Mateus Machado&nbsp;-&nbsp;
              <a 
                href="https://twitter.com/mateus_fmachado" 
                target="_blank" 
                rel="noopener noreferrer" >Twitter</a>&nbsp;-&nbsp;
              <a 
                href="https://github.com/mateusfmachado" 
                target="_blank" 
                rel="noopener noreferrer" >Github</a>&nbsp;
              </small>
          </div>);
}

class App extends Component {
  render() {
    return (
      <div className="App flex vertical flex-center">
        <div className="Container">
          <h1 className="title">CryptoConverter</h1>
          <br />
          <Panel />
          <br />
          <hr />
          <Footer />
        </div>
      </div>
    );
  }
}

export default App;



