import React, {Component} from 'react';

const FIATS = [
    "USD","AUD", "BGN", "BRL", "CAD", 
    "CNY", "CZK", "DKK", "GBP", "HKD", 
    "HRK", "HUF", "IDR", "ILS", "INR", 
    "JPY", "KRW", "MXN", "MYR", "NOK", 
    "NZD", "PHP", "PLN", "RON", "RUB", 
    "SEK", "SGD", "THB", "TRY", "ZAR", 
    "CHF", "EUR"
];

const COINS = [
    "BTC", "ETH", "BCH", "XRP", "LTC", 
    "DASH", "NEO", "XEM", "XMR", "MIOTA", 
    "ETC", "QTUM", "OMG", "ADA", "LSK", 
    "ZEC", "XLM", "BCC", "EOS", "USDT", 
    "WAVES", "HSR", "STRAT", "ARK", "KMD", 
    "BCN", "STEEM", "PPT", "ARDR"
];

const request = async (url) => {
    const request = await fetch(url);
    const json = await request.json();
    return json;
}

const fetchFiat = async (fiat = 'USD') => {
    return await request('https://api.fixer.io/latest?base='+fiat);       
}

const fetchCrypto = async (crypto = 'BTC') => {
    return await request('https://min-api.cryptocompare.com/data/price?fsym='+crypto+'&tsyms=USD');
}

class Panel extends Component {   
    
    constructor(props){
        super();
        this.state = {
            selectMainMoneyType: 'crypto',
            mainMoney: 'BTC',
            mainMoneyValue: 1,
            selectResultMoneyType: 'fiat',
            resultMoney: 'USD',
            resultMoneyValue: 1,
            mainToUSD: 1,
            resultToUSD: 1,
            loading: true
        }
    }

    async componentWillMount(){
        const {USD} = await fetchCrypto();
        this.setState({
            mainToUSD: USD
        }, this.toogleLoading());         
    }

    toogleLoading(){
        this.setState({ loading: !this.state.loading });
    }

    async changeFiatMoney (money, field = 'resultMoney'){
        if(!field) return;
        const result = await fetchFiat( money || 'USD' ) ;
        if(field === 'resultMoney'){
            this.setState({ resultToUSD: result.rates['USD'] || 1 });
        } else {
            this.setState({ mainToUSD: result.rates['USD'] || 1 });
        } 
    }

    async changeCryptoMoney (money, field = 'mainMoney'){
        if(!field) return;
        const result = await fetchCrypto( money || 'BTC' ) ;
        if(field === 'mainMoney'){
            this.setState({ mainToUSD: result['USD'] || 1 });
        } else {            
            this.setState({ resultToUSD: result['USD'] || 1 });
        }
    }

    updateMoney(money, field){
        if(field === 'mainMoney'){
            if(this.state.selectMainMoneyType === 'crypto'){
                this.changeCryptoMoney( money, 'mainMoney');
            } else {
                this.changeFiatMoney( money, 'mainMoney');
            }
        } else{
            if(this.state.selectResultMoneyType === 'crypto'){
                this.changeCryptoMoney( money, 'resultMoney');
            } else {
                this.changeFiatMoney( money, 'resultMoney');
            }
        }
    }

    onChangeMoney = (field, ev) => {    
        const money = ev.target.value;
        this.setState({ [field]: money }, () => {
            this.updateMoney(money, field);
        });
    }

    onChange = (field, ev) => {
        this.setState({ [field]: ev.target.value });
    }

    render(){
        return (<div className="flex vertical flex-center">
                    <div className="flex full-width">
                        <div className="flex-1 flex flex-center">
                            <select
                                value={this.state.mainMoney}
                                onChange={(ev) => this.onChangeMoney('mainMoney',ev)} >
                                {
                                    COINS.map((coin, idx) => (
                                        <option key={idx} value={coin}>{coin}</option>
                                    ))
                                }
                            </select>
                        </div>
                        <div className="flex-1 flex flex-center">
                            <input
                                type="number"
                                style={{width: "80%"}}
                                value={this.state.mainMoneyValue}
                                onChange={(ev) => this.onChange('mainMoneyValue', ev) } />
                        </div>
                    </div>
                    <div className="flex full-width">
                        <div className="flex-1 flex flex-center">
                        <select
                                value={this.state.resultMoney}
                                onChange={(ev) => this.onChangeMoney('resultMoney',ev)} >
                                {
                                    FIATS.map((coin, idx) => (
                                        <option key={idx} value={coin}>{coin}</option>
                                    ))
                                }
                            </select>
                        </div>
                        <div className="flex-1 flex flex-center">
                            <h2>
                                {
                                    this.state.loading ?
                                    '-':
                                    '$ '+( (this.state.resultMoneyValue * this.state.mainToUSD * this.state.mainMoneyValue)/this.state.resultToUSD || 0 ).toFixed(2)
                                }
                            </h2>
                        </div>
                    </div>
                </div>);
    }


}

export default Panel;


